import "@babel/polyfill";
import $ from 'jquery';
import Swiper from 'swiper';
import loader from './__modules/loader';
import header from './__modules/header';
import sectionAbout from './__modules/section--about';
import sectionProject from './__modules/section--project';
import sectionServices from './__modules/section--services';
import contact from './__modules/contact';
import footer from './__modules/footer';

document.addEventListener('DOMContentLoaded', () => {
	cursor();
	loader();
	darkMode();
	header();
	sectionAbout();
	sectionServices();
	sectionProject();
	contact();
	footer();
});

window.addEventListener('load', () => {
	if ($('.swiper').length > 0) {
		let swiper = new Swiper('.swiper', {
			slidesPerView: 1.1,
			centeredSlides: true,
			loop: true,
			speed: 400,
			spaceBetween: 10,
			breakpoints: {
				992: {
					slidesPerView: 1.5,
				},
			},
		});
	}
});

window.addEventListener('resize', () => {
});

window.addEventListener('scroll', () => {
});


//dark mode
function darkMode() {
	let icon = document.getElementById("icon");

	let darkMode = localStorage.getItem("darkMode");
	if (darkMode === "dark-theme") {
		document.body.classList.add("dark-theme");
		icon.innerHTML = `<span class="icon-sun"></span>`;
	} else {
		icon.innerHTML = `<span class="icon-moon"></span>`;
	}

	icon.onclick = function () {
		document.body.classList.toggle("dark-theme");

		if (document.body.classList.contains("dark-theme")) {
			icon.innerHTML = `<span class="icon-sun"></span>`;
			localStorage.setItem("darkMode", "dark-theme");
		} else {
			icon.innerHTML = `<span class="icon-moon"></span>`;
			localStorage.setItem("darkMode", "light-theme");
		}
	};
}

//ball on cursor
function cursor() {
	let touchDevice = "ontouchstart" in document.documentElement;

	if (!touchDevice) {
		document.body.innerHTML += `<div class="cursor"></div>`;
	}

	if ($(".cursor").length > 0) {
		const cursor = document.querySelector('.cursor');

		let cursorX = 0;
		let cursorY = 0;

		let mouseX = 0;
		let mouseY = 0;

		let speed = 0.15;

		function animate() {
			let distX = mouseX - cursorX;
			let distY = mouseY - cursorY;

			cursorX = cursorX + (distX * speed);
			cursorY = cursorY + (distY * speed);

			cursor.style.left = cursorX + "px";
			cursor.style.top = cursorY + "px";

			requestAnimationFrame(animate);
		}
		animate();

		document.addEventListener("mousemove", function (event) {
			mouseX = event.clientX;
			mouseY = event.clientY;
		});

		document.addEventListener("scroll", function () {
			cursor.style.left = (event.clientX + window.scrollX) + "px";
			cursor.style.top = (event.clientY + window.scrollY) + "px";
		});

		$(document).ready(function () {
			$(document).on("mouseenter", "button , a , #icon", function () {
				$(".cursor").css({ transform: "translate(-50%, -50%) scale(2.5)", transition: "0.1s ", background: "var(--cursor-color)", border: "none" })
			})
			$(document).on("mouseleave", "button , a, #icon", function () {
				$(".cursor").css({ transform: "translate(-50%, -50%) scale(1)", transition: "0.1s ", background: "transparent", border: "1px solid  grey" })
			})
		})
	}
}
