import $ from 'jquery';
import { gsap, TimelineMax, Power2, Power4, CSSRulePlugin, ScrollTrigger } from 'gsap/all';
require("../__vendor/SplitText.min.js");

gsap.registerPlugin(CSSRulePlugin, ScrollTrigger);

const footerEffect = () => {

    var lastSection = $('section:last');

    gsap.set('.footer-container', { yPercent: -50 })

    const footer = gsap.timeline({ paused:true })
    
    footer.to('.footer-container', { yPercent: 0, ease: 'none' });
    
    ScrollTrigger.create({  
      trigger: lastSection,
      start: 'top top',
      end: '90%',
      animation: footer,
      scrub: true,  
    })

};

export default footerEffect;