import $ from 'jquery';
import { gsap, TimelineMax, Power2, Power4, CSSRulePlugin, ScrollTrigger } from 'gsap/all';

gsap.registerPlugin(CSSRulePlugin, ScrollTrigger);

const sectionServices = () => {
    gsap.set(".services > h2", {
        xPercent: -50,
    });

    gsap.to('.services > h2', {
        xPercent: 70,
        ease: "none",
        scrollTrigger: {
            trigger: ".services",
            start: "0%",
            end: "100%",
            scrub: 2,
            smoothScrolling: true,
        }
    });

    let services = gsap.utils.toArray(".service--card");

    const calculateXPercent = () => {
      if (window.innerWidth < 768) {
        return -100 * (services.length - 0.8);
      } else {
        return -100 * (services.length - 2.5);
      }
    };
    
    gsap.to(services, {
      xPercent: calculateXPercent(),
      ease: "none",
      scrollTrigger: {
        trigger: ".services",
        start: "0%",
        end: "100%",
        pin: true,
        scrub: 1,
        end: () => "+=" + document.querySelector(".main--services").offsetWidth - window.innerWidth,
      }
    });
};

export default sectionServices;
