import $ from 'jquery';
import { gsap, TimelineMax, Power2, Power4, CSSRulePlugin, ScrollTrigger } from 'gsap/all';

gsap.registerPlugin(CSSRulePlugin, ScrollTrigger);

const contact = () => {

    let currentScroll = 0;
    let isScrollingDown = true;
    let arrows = document.querySelectorAll(".arrow")

    let tween = gsap.to(".contact > .contact--text > span", {
        xPercent: -150,
        repeat: -1,
        ease: "linear",
        duration: 10,
    });


    window.addEventListener("scroll", function () {
        if (window.pageYOffset > currentScroll) {
            isScrollingDown = true;
        } else {
            isScrollingDown = false;
        }

        gsap.to(tween, {
            timeScale: isScrollingDown ? 1 : -1,
        })

        arrows.forEach(arrow => {
            if (isScrollingDown) {
                gsap.to(arrow, { rotation: 90 });
            } else {
                gsap.to(arrow, { rotation: -90 });
            }
        });

        currentScroll = window.pageYOffset
    })

    const contactContent = document.querySelectorAll('.container > h2 , form input , textarea , form > button , .contact--box > h3 , .contact--box > h2');

    contactContent.forEach((paragraph) => {

        gsap.set(paragraph, {
            opacity: 0,
            y:30,
        });

        const contentTl = gsap.timeline({
            paused: true
        });

        contentTl.to(paragraph, {
            opacity: 1,
            y:0,
            duration:0.7,
            ease: Power2.ease
        });

        ScrollTrigger.create({
            trigger: paragraph,
            start: 'bottom bottom-=100',
            end: 'bottom center',
            animation: contentTl,
            toggleActions: 'play none none reverse'
        });
    });

};

export default contact;