import $ from 'jquery';
import { gsap, TimelineMax, Power2, Power4, CSSRulePlugin, ScrollTrigger } from 'gsap/all';

gsap.registerPlugin(CSSRulePlugin, ScrollTrigger);

const loader = () => {
    const loaderText = document.querySelectorAll(".loader");

    document.body.style.overflow = "hidden";

    let loaderTL = gsap.timeline();

    gsap.set(".logo, .header ul li, .email ", {
        autoAlpha: 1,
        y: -70,
    });

    gsap.set(".hero--section > span ", {
        opacity: 0,
        y: -100,
    });

    let splitText = new SplitText(".hero--section > h1", { type: "lines" });

    loaderText[0].textContent = 99;

    gsap.from(loaderText, {
        textContent: 0,
        duration: 3,
        ease: Power4.easeIn,
        snap: { textContent: 1 },
        onComplete: () => {
            gsap.delayedCall(0.5, startLoaderExitAnimation);
        },
    });

    const startLoaderExitAnimation = () => {
        loaderTL.to(".loader", 1, {
            x: "-100%",
            ease: Power4.easeIn,
        }).to('.logo', {
            autoAlpha: 1,
            y: 0,
        }, 0.7).to('.header ul li', {
            autoAlpha: 1,
            y: 0,
            stagger: 0.33
        }, 1.6).to('.email', {
            autoAlpha: 1,
            y: 0,
        }, 2).from(splitText.lines, {
            x: 300,
            stagger: 0.3,
            ease: Power2.ease,
            duration: 0.8,
            opacity: 0,
        }, 3).to('.hero--section > span', {
            opacity: 1,
            y: 0,
        }, 3.5).call(enableScroll);

    };


    function enableScroll() {
        $(".loader").remove();
        document.body.style.overflow = "auto"
    }
};

export default loader;
