import $ from 'jquery';
import { gsap, TimelineMax, Power2, Power4, CSSRulePlugin, ScrollTrigger } from 'gsap/all';
require("../__vendor/SplitText.min.js");

gsap.registerPlugin(CSSRulePlugin, ScrollTrigger); 

const header = () => {
   

    $(document).ready(function(){
        $(window).scroll(function(){
            let scrollTop = $(window).scrollTop();
    
            if(scrollTop > 10){
                $(".logo, .header ul li a, .email").addClass("active")
            }else{
                $(".logo, .header ul li a, .email").removeClass("active")
            }
        })

        function changeHeader() {
            let scrollDown = $(window).scrollTop();
            $(window).scroll(function () {
                let scrollUp = $(window).scrollTop();
              
                if (scrollUp > scrollDown && scrollUp > 100) {
                    $('.header').css({ transform: "translateY(-100%)", transition: ".3s ease-in-out" });
                } else {
                    $('.header').css({ transform: "translateY(0%)", transition: ".3s ease-in-out" });
                }
                scrollDown = scrollUp;
            });
        }

        changeHeader()
    })
};

export default header;