import { gsap, ScrollTrigger } from 'gsap/all';
require("../__vendor/SplitText.min.js");

gsap.registerPlugin(ScrollTrigger);

const sectionAbout = () => {

    const aboutLeftContent = document.querySelectorAll('.about--left-content > p');
    const aboutRightContent = document.querySelectorAll('.about--right-content > .box > h2, .about--right-content > .box > ul li, .about--right-content > .box > p');

    aboutLeftContent.forEach((paragraph) => {
        const splitText = new SplitText(paragraph, { type: "words" });

        gsap.set(splitText.words, {
            opacity: 0,
            y: 30,
            duration: 0.7
        });

        const contentTl = gsap.timeline({
            paused: true
        });

        contentTl.to(splitText.words, {
            opacity: 1,
            y: 0,
            stagger: 0.045,
        });

        ScrollTrigger.create({
            trigger: paragraph,
            start: 'top center',
            end: 'bottom center',
            animation: contentTl,
            toggleActions: 'play none none reverse'
        });
    });

    
    const splitTextRight = new SplitText(aboutRightContent, { type: "lines" });

    gsap.set(splitTextRight.lines, {
        opacity: 0,
        x: 50
    });

    const rightContentTl = gsap.timeline({
        paused: true
    });

    rightContentTl.to(splitTextRight.lines, {
        opacity: 1,
        x: 0,
        stagger: 0.215,
    });

    ScrollTrigger.create({
        trigger: '#section--about',
        start: 'top center',
        end: 'bottom center',
        animation: rightContentTl,
        toggleActions: 'play none none reverse'
    });
};
export default sectionAbout;