import { gsap, ScrollTrigger } from 'gsap/all';
require("../__vendor/SplitText.min.js");

gsap.registerPlugin(ScrollTrigger);

const sectionProject = () => {

    const projectTitle = document.querySelectorAll('.section--project > .project--title');
    const projectInfo = document.querySelectorAll('.project--info-left , .project--info-right > p, .project--info-right > ul li');
    const swiper = document.querySelectorAll('.mySwiper');

    projectTitle.forEach((paragraph) => {
        const splitText = new SplitText(paragraph, { type: "lines" });

        gsap.set(splitText.lines, {
            opacity: 0,
            x: 150
        });

        const contentTl = gsap.timeline({
            paused: true
        });

        contentTl.to(splitText.lines, {
            opacity: 1,
            x: 0,
            stagger: 0.15,
        });

        ScrollTrigger.create({
            trigger: paragraph,
            start: 'bottom bottom-=100',
            end: 'bottom center',
            animation: contentTl,
            toggleActions: 'play none none reverse'
        });
    });
    
    swiper.forEach((swiper) => {

        gsap.set(swiper, {
            opacity: 0,
            y: 70
        });

        const contentTl = gsap.timeline({
            paused: true
        });

        contentTl.to(swiper, {
            opacity: 1,
            y:0,
            duration:1,
            stagger: 0.15,
        });

        ScrollTrigger.create({
            trigger: swiper,
            start: 'top center',
            end: 'bottom center',
            animation: contentTl,
            toggleActions: 'play none none reverse'
        });
    });


    projectInfo.forEach((paragraph) => {
        const splitText = new SplitText(paragraph, { type: "lines" });

        gsap.set(splitText.lines, {
            opacity: 0,
        });

        const contentTl = gsap.timeline({
            paused: true
        });

        contentTl.to(splitText.lines, {
            opacity: 1,
            duration:1,
            stagger: 0.15,
        });

        ScrollTrigger.create({
            trigger: paragraph,
            start: 'top center',
            end: 'bottom center',
            animation: contentTl,
            toggleActions: 'play none none reverse'
        });
    });

};
export default sectionProject;